import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api.service';
import { environment } from '../../environments/environment';
import { interval, Subscription } from 'rxjs';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {

  constructor( private api: ApiService ) { }
  // Variables
  isEmirleriListesiSubscription: Subscription;
  aktifIsEmriSubscription: Subscription;
  vardiyaBilgisi: any;
  isEmirleriListesi: any;
  fireSebepleri: any;
  durusSebepleri: any;
  menuOpen = false;
  menuItems: any;
  menuTitle: any;
  aktifIsEmri: any;
  isEmriDetay: any;
  popup: any;
  durusSebebi: any;
  fireSebebi: any;
  selectedIsEmri: any;
  ngOnInit(): void {
    // Call all required functions on init
    this.getVardiyaBilgisi();
    this.getIsEmirleriListesi();
    this.getFireSebepleri();
    this.getDurusSebepleri();
    this.getAktifIsEmri();

    const isEmirleriListesiInterval = interval(environment.isEmirleriListesiKontrol * 1000); // Set interval for iş emir listesi check freq.
    this.isEmirleriListesiSubscription = isEmirleriListesiInterval.subscribe(val => this.getIsEmirleriListesi());
    const aktifIsEmriInterval = interval(environment.isEmriKontrol * 1000); // Set interval for aktif iş emri check freq.
    this.aktifIsEmriSubscription = aktifIsEmriInterval.subscribe(val => this.getAktifIsEmri());
  }

  // Functions
  getIsEmirleriListesi() { // İş Emir Listesini Getirir
    this.api.get('/v3/data/devices/' + environment.deviceId + '/methods/isEmirleriListesiGetir').subscribe( data => {
      this.isEmirleriListesi = data;
    }, err => {
      console.log(err);
    });
  }

  getVardiyaBilgisi() { // Vardiya bilgisini getirir
    this.api.get('/v3/data/devices/' + environment.deviceId + '/methods/vardiyaBilgisiGetir').subscribe( data => {
      this.vardiyaBilgisi = data;
    }, err => {
      console.log(err);
    });
  }

  getFireSebepleri() { // Menü için fire sebeplerini getirir
    this.api.get('/v3/data/devices/' + environment.deviceId + '/methods/fireSebepleri').subscribe( data => {
      this.fireSebepleri = data;
    }, err => {
      console.log(err);
    });
  }

  getDurusSebepleri() { // Menü için fire sebeplerini getirir
    this.api.get('/v3/data/devices/' + environment.deviceId + '/methods/durusSebepleri').subscribe( data => {
      this.durusSebepleri = data;
    }, err => {
      console.log(err);
    });
  }

  getAktifIsEmri() {
    this.api.get('/v3/data/devices/' + environment.deviceId + '/methods/aktifIsEmri').subscribe( data => {
      if(data.value) {
        this.aktifIsEmri = data.value;
        this.isEmriBilgisiGetir(this.aktifIsEmri);
      } else {
        this.aktifIsEmri = '';
        this.isEmriDetay = '';
      }
    }, err => {
      console.log(err);
    });
  }

  isEmriBilgisiGetir(value) {
    this.api.post('/v3/data/devices/' + environment.deviceId + '/methods/isEmriBilgisiGetir', { "value": value }).subscribe( data => {
      this.isEmriDetay = data.result;
    }, err => {
      console.log(err);
    });
  }

  isEmriBitir(value) {
    this.api.post('/v3/data/devices/' + environment.deviceId + '/methods/isEmriBitir', { "value": value }).subscribe( data => {
    }, err => {
      console.log(err);
    });
  }

  isEmriDurdur(value) {
    this.api.post('/v3/data/devices/' + environment.deviceId + '/methods/isEmriDurdur', { "value": value }).subscribe( data => {
    }, err => {
      console.log(err);
    });
  }

  isEmriBaslat(value) {
    this.api.post('/v3/data/devices/' + environment.deviceId + '/methods/isEmriBaslat', { "value": value }).subscribe( data => {
      this.getAktifIsEmri();
    }, err => {
      console.log(err);
    });
  }

  durusSebebiGir(value) {
    this.api.post('/v3/data/devices/' + environment.deviceId + '/methods/durusSebebiGir', { "value": value }).subscribe( data => {
    }, err => {
      console.log(err);
    });
  }

  fireSebebiGir(value) {
    this.api.post('/v3/data/devices/' + environment.deviceId + '/methods/durusSebebiGir', { "value": value }).subscribe( data => {
    }, err => {
      console.log(err);
    });
  }

  openMenu(type) {
    if(this.aktifIsEmri) {
      this.menuOpen = true;
      this.durusSebebi = '';
      this.fireSebebi = '';
      if(type === 'durus') {
        this.menuTitle = 'Duruş Sebebi Gir';
        this.menuItems = this.durusSebepleri;
      } else if(type === 'fire') {
        this.menuTitle = 'Fire Sebebi Gir';
        this.menuItems = this.fireSebepleri;
      }
    } else {
      this.openPopup('aktifIsYok')
    }

  }

  openPopup(type) {
    if(type === 'iptal') {
      this.popup = {
        text: "İş emrini iptal etmek istediğinize emin misiniz?",
        type: type
      }
    }
    if(type === 'durdur') {
      this.popup = {
        text: "İş emrini durdurmak istediğinize emin misiniz?",
        type: type
      }
    }
    else if(type === 'durus') {
      this.popup = {
        text: "Seçtiğiniz duruş sebebini onaylıyor musunuz?",
        type: type
      }
    }
    else if(type === 'fire') {
      this.popup = {
        text: "Seçtiğiniz fire sebebini onaylıyor musunuz?",
        type: type
      }
    }
    else if(type === 'baslat') {
      this.popup = {
        text: "İş emrini başlatmak istediğinize emin misiniz?",
        type: type
      }
    }
    else if(type === 'aktifIsYok') {
      this.popup = {
        text: "Çalışan iş emri bulunamadı!",
        type: type
      }
    }
  }

  closePopup() {
    this.popup = '';
  }

  approvePopup() {
    if(this.popup.type === 'iptal') {
      this.isEmriBitir(this.aktifIsEmri)
    }
    else if(this.popup.type === 'durdur') {
      this.isEmriDurdur(this.aktifIsEmri)
    }
    else if(this.popup.type === 'baslat') {
      this.isEmriBaslat(this.aktifIsEmri)
    }
    else if(this.popup.type === 'durus') {
      let body = {
        ...this.durusSebebi,
        isEmriNo: this.aktifIsEmri
      }
      this.durusSebebiGir(body)
    }
    else if(this.popup.type === 'fire') {
      let body = {
        ...this.fireSebebi,
        isEmriNo: this.aktifIsEmri
      }
      this.fireSebebiGir(body)
    }
    this.closePopup();
    this.menuOpen = false;
  }

  activateIsEmri(value) {
    if(!this.aktifIsEmri) {
      this.selectedIsEmri = value?.isEmriNo;
    }
  }

  ngOnDestroy(): void {
    this.isEmirleriListesiSubscription.unsubscribe();
    this.aktifIsEmriSubscription.unsubscribe();
  }

}
