import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from './../environments/environment';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  constructor(private http: HttpClient) { }

  get(endpoint: any): Observable<any>{
    const headers = new HttpHeaders()
      .set('Content-Type', 'application/json')
      .append('Authorization', 'Basic ' + btoa(environment.apiKey + ':' + environment.apiPassword))
    return this.http.get(environment.apiUrl + endpoint, {headers})
  }

  post(endpoint: any, body: any): Observable<any>{
    const headers = new HttpHeaders()
      .set('Content-Type', 'application/json')
      .append('Authorization', 'Basic ' + btoa(environment.apiKey + ':' + environment.apiPassword))
    return this.http.post(environment.apiUrl + endpoint, body, {headers})
  }
}
